import React from 'react';
// project imports
import MainCard from 'ui-component/cards/MainCard';
import { Grid, FormControlLabel, Radio, RadioGroup, FormControl, Button } from '@mui/material';
import Box from '@mui/material/Box';
// ==============================|| TYPOGRAPHY ||============================== //

const ManifestManagementList = () => {
    const [shippingLabel, setShippingLabel] = React.useState('thermalLabelPrinters');

    const handleChange = (event) => {
        setShippingLabel(event.target.value);
    };

    const onSubmit = () => {
        console.log('Hello1', shippingLabel);
    };

    return (
        <MainCard title="Lable Setting">
            <Box sx={{ width: '100%' }}>
                <div style={{ marginBottom: 16, float: 'left', color: '#000', fontWeight: 'bold' }}>Set your shipping label format</div>
                <br />
                <Grid style={{ display: 'flex', marginTop: '5%', marginBottom: '5%' }}>
                    <FormControl>
                        <RadioGroup
                            aria-labelledby="demo-radio-buttons-group-label"
                            value={shippingLabel}
                            name="radio-buttons-group"
                            onChange={handleChange}
                            style={{ display: 'flex', flexDirection: 'row' }}
                        >
                            <FormControlLabel
                                value="standardDesktopPrinters"
                                control={<Radio />}
                                label="Standard Desktop Printers - Size A4 (8X11)"
                            />
                            <FormControlLabel
                                value="thermalLabelPrinters"
                                control={<Radio />}
                                label="Thermal Label Printers - Size (4X6)"
                            />
                        </RadioGroup>
                    </FormControl>
                </Grid>
                <Grid style={{ display: 'flex', marginTop: '5%', marginBottom: '5%', justifyContent: 'center' }}>
                    <Button variant="contained" color="primary" onClick={onSubmit} size="medium" style={{ marginRight: '5px' }}>
                        Submit
                    </Button>
                </Grid>
            </Box>
        </MainCard>
    );
};
export default ManifestManagementList;
